.alignleft {

	/*rtl:ignore*/
	float: left;

	/*rtl:ignore*/
	margin-right: 1.5em;
	margin-bottom: 1.5em;
}

.alignright {

	/*rtl:ignore*/
	float: right;

	/*rtl:ignore*/
	margin-left: 1.5em;
	margin-bottom: 1.5em;
}

.aligncenter {
	clear: both;

	@include center-block;

	margin-bottom: 1.5em;
}

.alignwide, .alignfull {
    margin-left: auto;
    margin-right: auto;
}


.alignwide {
    max-width: 1200px;  
}

// Full Width Block
.alignfull {
    width: 100vw;
    position: relative;
    left: 50%;
    // right: 50%;
    margin-left: -50vw;
    // margin-right: -50vw;
}
